@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;



.bg-theme{
    background-image: linear-gradient(0.2deg, rgba(40,173,222,1) 4.8%, rgb(27,76,224) 85.5%) ;
  }
  
  .clip-path{
    clip-path: polygon(0 0, 100% 0,100% 100%, 0 65%);
  }
  
  .nike-container{
   @apply w-11/12 m-auto;
  }
  
  .button-theme {
    @apply px-8 py-1  active:scale-90 
    transition-all duration-100 ease-in-out
    /* shadow-md sm:text-sm */
    
  }

  .button-theme_3 {
    @apply px-2 py-1  active:scale-90 
    transition-all duration-100 ease-in-out
    /* shadow-md sm:text-sm */
  }

  .button-theme_2 {
    @apply px-0.5 py-0.5  active:scale-90 
    transition-all duration-100 ease-in-out
    /* shadow-md sm:text-sm */
  }
 .transitions-theme{
  @apply transition-all duration-700 ease-in-out cursor-pointer
 }
 .icon-style {
  @apply h-6 w-6 sm:h-5 sm:w-5 text-slate-100 cursor-pointer transition-all duration-100 active:scale-110 leading-tight;
}

.icon-style_2 {
  @apply h-6 w-6 sm:h-5 sm:w-5 text-slate-900 cursor-pointer transition-all duration-100 active:scale-110 leading-tight;
}
 

