.blur-effect-theme {
  @apply bg-white/70 bg-clip-padding backdrop-filter backdrop-blur bg-opacity-50 opacity-100 z-[100] shadow-sm shadow-slate-100 transition-all duration-300;
}

.blur-effect-theme_2 {
  @apply bg-white/60 bg-clip-padding backdrop-filter backdrop-blur bg-opacity-50 opacity-100 z-[200] shadow-sm shadow-slate-100 transition-all duration-300;
}